import React, {useState} from 'react'
import SuperEditableSpan from './common/c4-SuperEditableSpan/SuperEditableSpan'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import {restoreState, saveState} from './localStorage/localStorage'
import logo from './common/c4-SuperEditableSpan/pencil.png'

function HW6() {
    const [value, setValue] = useState<string>('')

    const save = () => {
        saveState<string>('editable-span-value', value)
    }
    const restore = () => {
        setValue(restoreState('editable-span-value', ''))
    }

    const logoStyle = {
        backgroundColor: 'transparent',
        width: '15px',
        height: '15px'
    }

    const divStyle = {
        color: '#99ff99',
        marginLeft: '10px',
    }
    const comment = <div style={divStyle}><img src={logo} style={logoStyle} alt={'image'}/> enter text...</div>

    return (
        <div>
            <hr/>
            homeworks 6

            {/*should work (должно работать)*/}
            <div style={divStyle}>
                <SuperEditableSpan
                    value={value}
                    onChangeText={setValue}
                    spanProps={{children: value ? undefined : comment}}
                />
            </div>
            <SuperButton onClick={save}>save</SuperButton>
            <SuperButton onClick={restore}>restore</SuperButton>

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperEditableSpan/>*/}
            <hr/>
        </div>
    )
}

export default HW6
